import React, { useState, useEffect, useCallback } from 'react';
import { supabase } from '../../lib/supabaseClient';
import { SLOTS, getEndTime } from '../../utils/validators';

const Calendar = ({ onSelectSlot }) => {
  const [reservations, setReservations] = useState([]);
  const [loading, setLoading] = useState(true);
  const [startDate, setStartDate] = useState(new Date());
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const getDays = () => {
    const dates = [];
    const start = new Date(startDate);
    start.setHours(0, 0, 0, 0);
    
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    
    const maxDate = new Date(today);
    maxDate.setDate(maxDate.getDate() + 7);
    
    const daysToShow = isMobile ? 3 : 7;
    
    for (let i = 0; i < daysToShow; i++) {
      const date = new Date(start);
      date.setDate(date.getDate() + i);
      
      // Solo aadir si est dentro del rango permitido (hoy hasta +7 das)
      if (date <= maxDate && date >= today) {
        dates.push(formatDate(date));
      }
    }
    
    return dates;
  };

  const formatDate = (date) => {
    const d = new Date(date);
    const year = d.getFullYear();
    const month = String(d.getMonth() + 1).padStart(2, '0');
    const day = String(d.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
  };

  const dates = getDays();

  const fetchReservations = useCallback(async () => {
    try {
      const result = await supabase
        .from('reservas')
        .select('*')
        .eq('status', 'activa')
        .in('fecha', dates);

      if (result.error) {
        console.error('Error fetching reservations:', result.error);
      }

      setReservations(result.data || []);
    } catch (error) {
      console.error('Error fetching reservations:', error);
    } finally {
      setLoading(false);
    }
  }, [dates]);

  useEffect(() => {
    fetchReservations();
  }, [fetchReservations]);

  const getReservationForSlot = (date, slot) => {
    return reservations.find(r => r.fecha === date && r.hora_inicio === slot);
  };

  const getDayName = (dateStr) => {
    const days = ['Dom', 'Lun', 'Mar', 'Mi', 'Jue', 'Vie', 'Sb'];
    const date = new Date(dateStr + 'T12:00:00');
    return days[date.getDay()];
  };

  const getMonthYear = (dateStr) => {
    const months = ['Ene', 'Feb', 'Mar', 'Abr', 'May', 'Jun', 'Jul', 'Ago', 'Sep', 'Oct', 'Nov', 'Dic'];
    const date = new Date(dateStr + 'T12:00:00');
    return `${months[date.getMonth()]} '${String(date.getFullYear()).slice(-2)}`;
  };

  const goToPrevious = () => {
    const newDate = new Date(startDate);
    const daysToMove = isMobile ? 3 : 7;
    newDate.setDate(newDate.getDate() - daysToMove);
    
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    
    // No permitir ir antes de hoy
    if (newDate < today) {
      newDate.setTime(today.getTime());
    }
    
    setStartDate(newDate);
  };

  const goToNext = () => {
    const newDate = new Date(startDate);
    const daysToMove = isMobile ? 3 : 7;
    newDate.setDate(newDate.getDate() + daysToMove);
    
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    
    const maxDate = new Date(today);
    maxDate.setDate(maxDate.getDate() + 7);
    
    // No permitir ir ms all de +7 das desde el da actual
    const lastDayOfNewRange = new Date(newDate);
    lastDayOfNewRange.setDate(lastDayOfNewRange.getDate() + daysToMove - 1);
    
    if (lastDayOfNewRange > maxDate) {
      // Ajustar para que el ltimo da mostrado sea exactamente maxDate
      const adjustedDate = new Date(maxDate);
      adjustedDate.setDate(adjustedDate.getDate() - (daysToMove - 1));
      setStartDate(adjustedDate);
    } else {
      setStartDate(newDate);
    }
  };

  const goToToday = () => {
    setStartDate(new Date());
  };

  const canGoNext = () => {
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    
    const maxDate = new Date(today);
    maxDate.setDate(maxDate.getDate() + 7);
    
    const daysToMove = isMobile ? 3 : 7;
    const lastCurrentDay = new Date(startDate);
    lastCurrentDay.setDate(lastCurrentDay.getDate() + daysToMove - 1);
    
    return lastCurrentDay < maxDate;
  };

  const canGoPrevious = () => {
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    
    const firstCurrentDay = new Date(startDate);
    firstCurrentDay.setHours(0, 0, 0, 0);
    
    return firstCurrentDay > today;
  };

  if (loading) return <div className="loading">Cargando calendario</div>;

  return (
    <div className="calendar-container">
      <div className="calendar-header-section">
        <h2>Disponibilidad de Pista</h2>
        <div className="calendar-controls">
          <button onClick={goToPrevious} disabled={!canGoPrevious()}> Anterior</button>
          <button onClick={goToToday}>Hoy</button>
          <button onClick={goToNext} disabled={!canGoNext()}>Siguiente </button>
        </div>
      </div>

      <div className="calendar-grid-wrapper">
        <div className="calendar-grid" data-mobile={isMobile}>
          <div className="calendar-header">
            <div className="time-column-header">Horario</div>
            {dates.map(date => (
              <div key={date} className="date-column">
                <div className="day-name">{getDayName(date)}</div>
                <div className="date-info">{new Date(date + 'T12:00:00').getDate()}</div>
                <div className="date-month-year">{getMonthYear(date)}</div>
              </div>
            ))}
          </div>

          {SLOTS.map(slot => (
            <div key={slot} className="calendar-row">
              <div className="time-cell">
                {slot}
                <span className="time-end">- {getEndTime(slot)}</span>
              </div>
              {dates.map(date => {
                const reservation = getReservationForSlot(date, slot);
                
                return (
                  <div 
                    key={`${date}-${slot}`} 
                    className={`slot-cell ${reservation ? 'occupied' : 'available'}`}
                    onClick={() => !reservation && onSelectSlot(date, slot)}
                    title={reservation ? `${reservation.nombre} ${reservation.apellidos} - Portal ${reservation.portal}, ${reservation.piso}${reservation.letra}` : 'Disponible - Click para reservar'}
                  >
                    {reservation ? (
                      <div className="reservation-info">
                        <div className="name">
                          {reservation.nombre} {reservation.apellidos}
                        </div>
                        <div className="location">
                          P{reservation.portal}  {reservation.piso}{reservation.letra}
                        </div>
                      </div>
                    ) : (
                      <div className="available-label">Libre</div>
                    )}
                  </div>
                );
              })}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Calendar;
